.div-gray {
  padding-top: 1vh;
  padding-left: 19.5vh;
  background-color: rgb(240, 241, 246);
  padding-right: 19.5vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

@media (max-width: 1440px) {
  .div {
    display: flex;
    /* flex-wrap: wrap; */
    /* padding: 1vh 10vh; */
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  .text-title {
    font-family: "Poppins", sans-serif;
    font-size: 8vh;
    margin-bottom: -2vw;
  }

  .main-text {
    font-family: "Poppins", sans-serif;
    color: hsl(0, 0%, 75%);
    padding-right: 2vh;
    font-size: 2.5vh;
    margin-bottom: 4vh;
  }
  .content {
    /* padding-right: 1vh; */
    /* margin-right: 50; */
    position: relative;
    /* display: inline-block; */
  }

  .div-img {
    position: static;
    margin-bottom: 6vh;
  }

  .div-url-main {
    background-color: hsl(257, 27%, 26%);
    display: inline-block;
    border-radius: 2vh;
    height: 21.3vh;
  }

  .div-url {
    position: relative;
    left: 0px;
    top: -8vw;
  }

  .background-url {
    border-radius: 2vh;
  }

  .div-shorten {
    position: relative;
    bottom: 12vh;
  }

  .text-secundary {
    font-family: "Poppins", sans-serif;
    color: hsl(0, 0%, 75%);
    font-size: 2vh;
    text-align: center;
  }

  .div-text-secundary {
    padding-left: 40vh;
    padding-right: 40vh;
    margin-bottom: 8vh;

    /* justify-content: center; */
    /* align-items: center; */
  }
  .title-p {
    text-align: center;
    margin-bottom: 0;
  }

  .cards {
    display: flex;
    /* background-color: hsl(180, 66%, 49%); */
    justify-content: space-between;
    width: 140vh;
    padding-bottom: 18vh;
  }

  .div-end {
    background-color: hsl(257, 27%, 26%);
    display: inline-block;
    height: 31.9vh;
  }

  .div-end-2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    left: 3vh;
    top: -25vh;
  }

  .title-end {
    text-align: center;
    color: white;
    font-family: "Poppins", sans-serif;
  }

  .div-end-dark {
    padding-top: 5vh;
    background-color: hsl(260, 8%, 14%);
    display: flex;
    width: 100vw;
    justify-content: space-around;
    align-items: flex-start;
    padding-bottom: 8vh;
    /* flex-direction: row; */
  }

  .tittle-shortly {
    font-family: "Poppins", sans-serif;
    color: white;
    line-height: 0vw;
  }

  .div-end-dark-1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* justify-content: center;
    align-items: center; */
  }

  .div-end-dark-0 {
    display: flex;
    align-items: flex-start;
    padding-right: 12vh;
  }

  .div-end-dark-icons {
    display: flex;
    justify-content: space-around;
    width: 12vw;
  }

  .tittle-end {
    font-family: "Poppins", sans-serif;
    color: white;
    font-size: 1.2vw;
    padding-bottom: 1vh;
  }
  /* cuando un input es requerido */
  /* .url-input:required {
    border: 2px solid yellow;
  } */
}
@media (max-width: 375px) {
  .div {
    display: flex;
    flex-direction: column-reverse;
    /* flex-wrap: wrap; */
    /* padding: 1vh 10vh; */
    justify-content: space-between;
    align-items: center;
  }
  .main-text {
    font-family: "Poppins", sans-serif;
    color: hsl(0, 88%, 38%);
  }
}
